shiny-chat-container {
  --shiny-chat-border: var(--bs-border-width, 1px) solid var(--bs-border-color, #e9ecef);
  --shiny-chat-user-message-bg: RGBA(var(--bs-primary-rgb, 0, 123, 194), 0.06);
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  gap: 1rem;
  overflow: auto;
}
shiny-chat-container p:last-child {
  margin-bottom: 0;
}
shiny-chat-container shiny-chat-messages {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  /* Align the user message to the right */
}
shiny-chat-container shiny-chat-messages shiny-chat-message {
  display: grid;
  grid-template-columns: auto minmax(0, 1fr);
  gap: 1rem;
  /* Vertically center the 2nd column (message content) */
}
shiny-chat-container shiny-chat-messages shiny-chat-message > * {
  height: fit-content;
}
shiny-chat-container shiny-chat-messages shiny-chat-message .message-icon {
  border-radius: 50%;
  border: var(--shiny-chat-border);
}
shiny-chat-container shiny-chat-messages shiny-chat-message .message-icon > * {
  margin: 0.5rem;
  height: 20px;
  width: 20px;
}
shiny-chat-container shiny-chat-messages shiny-chat-message .message-content {
  align-self: center;
}
shiny-chat-container shiny-chat-messages shiny-user-message {
  align-self: flex-end;
  padding: 0.75rem 1rem;
  border-radius: 10px;
  background-color: var(--shiny-chat-user-message-bg);
}
shiny-chat-container shiny-chat-input {
  margin-top: auto;
  position: sticky;
  background-color: var(--bs-body-bg, white);
  bottom: 0;
  padding: 0.25rem;
}
shiny-chat-container shiny-chat-input textarea {
  --bs-border-radius: 26px;
  resize: none;
  padding-right: 36px !important;
  max-height: 175px;
}
shiny-chat-container shiny-chat-input textarea::placeholder {
  color: var(--bs-gray-600, #707782) !important;
}
shiny-chat-container shiny-chat-input button {
  position: absolute;
  bottom: 10px;
  right: 11px;
  background-color: transparent;
  color: var(--bs-primary, #007bc2);
  transition: color 0.25s ease-in-out;
  border: none;
  padding: 0;
  cursor: pointer;
  line-height: 16px;
  border-radius: 50%;
}
shiny-chat-container shiny-chat-input button:disabled {
  cursor: not-allowed;
  color: var(--bs-gray-500, #8d959e);
}

/*
  Disable the page-level pulse when the chat input is disabled
  (i.e., when a response is being generated and brought into the chat)
*/
.shiny-busy:has(shiny-chat-input[disabled])::after {
  display: none;
}

/* Code highlighting (for both light and dark mode) */
pre code.hljs {
  display: block;
  overflow-x: auto;
  padding: 1em;
}

code.hljs {
  padding: 3px 5px;
}

/*

Atom One Light by Daniel Gamage
Original One Light Syntax theme from https://github.com/atom/one-light-syntax

base:    #fafafa
mono-1:  #383a42
mono-2:  #686b77
mono-3:  #a0a1a7
hue-1:   #0184bb
hue-2:   #4078f2
hue-3:   #a626a4
hue-4:   #50a14f
hue-5:   #e45649
hue-5-2: #c91243
hue-6:   #986801
hue-6-2: #c18401

*/
pre:has(> code.hljs) {
  color: #383a42;
  background: #fafafa;
}

.hljs-comment,
.hljs-quote {
  color: #a0a1a7;
  font-style: italic;
}

.hljs-doctag,
.hljs-keyword,
.hljs-formula {
  color: #a626a4;
}

.hljs-section,
.hljs-name,
.hljs-selector-tag,
.hljs-deletion,
.hljs-subst {
  color: #e45649;
}

.hljs-literal {
  color: #0184bb;
}

.hljs-string,
.hljs-regexp,
.hljs-addition,
.hljs-attribute,
.hljs-meta .hljs-string {
  color: #50a14f;
}

.hljs-attr,
.hljs-variable,
.hljs-template-variable,
.hljs-type,
.hljs-selector-class,
.hljs-selector-attr,
.hljs-selector-pseudo,
.hljs-number {
  color: #986801;
}

.hljs-symbol,
.hljs-bullet,
.hljs-link,
.hljs-meta,
.hljs-selector-id,
.hljs-title {
  color: #4078f2;
}

.hljs-built_in,
.hljs-title.class_,
.hljs-class .hljs-title {
  color: #c18401;
}

.hljs-emphasis {
  font-style: italic;
}

.hljs-strong {
  font-weight: bold;
}

.hljs-link {
  text-decoration: underline;
}

[data-bs-theme=dark] {
  /*

