[data-bs-theme=dark] .hljs-selector-tag,
[data-bs-theme=dark] .hljs-deletion,
[data-bs-theme=dark] .hljs-subst {
  color: #e06c75;
}
[data-bs-theme=dark] .hljs-literal {
