}
[data-bs-theme=dark] .hljs-link {
  text-decoration: underline;
}

/*
  Styling for the code-copy button (inspired by Quarto's code-copy feature)
*/
pre:has(.code-copy-button) {
  position: relative;
}

.code-copy-button {
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  margin-top: 5px;
  margin-right: 5px;
  background-color: transparent;
}
.code-copy-button > .bi {
  display: flex;
  gap: 0.25em;
}
